'use client';

import React from 'react';
import Image from 'next/image';
import Link from 'next/link';
import { useParams, useRouter } from 'next/navigation';
import { useAccount } from 'wagmi';
import { useEffect } from 'react';
import { useGameState } from '@/hooks/useGameState';
import { useTokenBalance } from '@/hooks/useTokenBalance';
import ConnectWalletButton from '@/components/ConnectWalletButton';

export default function RoomPage() {
  const params = useParams();
  const router = useRouter();
  const { address, isConnected } = useAccount();
  const gameState = useGameState();
  const { apeBalance, bitBalance } = useTokenBalance();

  // Redirect if not connected or trying to access someone else's room
  useEffect(() => {
    if (!isConnected) {
      router.push('/');
    } else if (address && params?.address && 
      (typeof params.address === 'string') && 
      address.toLowerCase() !== params.address.toLowerCase()) {
      router.push(`/room/${address}`);
    }
  }, [isConnected, address, params?.address, router]);

  if (!isConnected || !address) {
    return null;
  }

  return (
    <div className="min-h-screen flex flex-col">
      {/* Header */}
      <header className="flex justify-between items-center px-6 py-4">
        <div className="flex items-center gap-8">
          <div className="flex items-center">
            <Image
              src="/bitape-logo.svg"
              alt="BitApe Logo"
              width={32}
              height={32}
              className="mr-2 pixel-art"
            />
            <span className="font-press-start text-banana text-xl">bitape</span>
          </div>
          <nav className="flex gap-8">
            <Link href="/about" className="font-press-start text-white hover:text-banana">
              ABOUT
            </Link>
            <Link href="/trade" className="font-press-start text-white hover:text-banana">
              TRADE $BIT
            </Link>
            <Link href="/leaderboard" className="font-press-start text-[#4A5568] hover:text-banana">
              LEADERBOARD
            </Link>
          </nav>
        </div>
        <ConnectWalletButton className="px-6 py-2 font-press-start" />
      </header>

      {/* Resources Section */}
      <div className="p-6">
        <h2 className="font-press-start text-banana mb-4">RESOURCES</h2>
        <div className="space-y-2 text-white font-press-start">
          <div className="flex items-center gap-2">
            <span className="text-banana">üíé</span>
            <span>{apeBalance} APE</span>
          </div>
          <div className="flex items-center gap-2">
            <span className="text-banana">ü™ô</span>
            <span>{bitBalance} BIT</span>
          </div>
          <div className="flex items-center gap-2">
            <span className="text-banana">üè†</span>
            <span>0 SPACES LEFT</span>
          </div>
          <div className="flex items-center gap-2">
            <span className="text-banana">‚ö°</span>
            <span>0 GIGAWATTS AVAILABLE</span>
          </div>
        </div>
      </div>

      {/* Main Content */}
      <main className="flex-grow flex flex-col items-center justify-center p-4 relative">
        {/* Grid Background */}
        <div className="absolute inset-0 bg-royal"
          style={{
            backgroundImage: `
              linear-gradient(to right, rgba(255,255,255,0.05) 1px, transparent 1px),
              linear-gradient(to bottom, rgba(255,255,255,0.05) 1px, transparent 1px)
            `,
            backgroundSize: '24px 24px',
            imageRendering: 'pixelated'
          }}
        />

        <div className="relative z-10 max-w-4xl mx-auto text-center">
          <h1 className="text-4xl md:text-6xl font-press-start mb-4 text-banana pixel-text">
            MINING FACILITY: {address.slice(0, 6)}...{address.slice(-4)}
          </h1>
          
          <p className="text-2xl md:text-3xl font-press-start mb-8 text-banana pixel-text">
            MINING STATS
          </p>
          
          <div className="grid grid-cols-1 md:grid-cols-2 gap-8 mb-12">
            <div className="bg-royal-blue p-6 rounded pixel-border">
              <div className="space-y-4 text-white font-press-start">
                <div className="flex items-center gap-2">
                  <span className="text-banana pixel-art">‚ö°</span>
                  <span>{gameState.hashRate} GH/s</span>
                </div>
                <div className="flex items-center gap-2">
                  <span className="text-banana pixel-art">üìà</span>
                  <span>{gameState.miningRate} BIT/day</span>
                </div>
              </div>
            </div>
            
            <div className="bg-royal-blue p-6 rounded pixel-border">
              <div className="space-y-4 text-white font-press-start">
                <div className="flex items-center gap-2">
                  <span className="text-banana pixel-art">üî•</span>
                  <span>{gameState.networkHashRatePercentage}% Network</span>
                </div>
                <div className="flex items-center gap-2">
                  <span className="text-banana pixel-art">‚õèÔ∏è</span>
                  <span>{gameState.minedBit} BIT Mined</span>
                </div>
              </div>
            </div>
          </div>

          <div className="mb-12">
            <div className="relative w-[200px] h-[200px] mx-auto border-2 border-banana pixel-border">
              <Image
                src="/globe.svg"
                alt="Globe Animation"
                fill
                priority
                className="object-contain pixel-art"
              />
            </div>
          </div>

          <div className="w-full max-w-md mx-auto space-y-4">
            {!gameState.hasFacility ? (
              <button
                onClick={() => gameState.purchaseFacility()}
                disabled={gameState.isPurchasingFacility}
                className="w-full font-press-start py-4 px-8 bg-transparent border-2 border-banana text-banana hover:bg-banana hover:text-royal transition-colors pixel-button"
              >
                {gameState.isPurchasingFacility ? 'PURCHASING...' : 'PURCHASE FACILITY (10 APE)'}
              </button>
            ) : (
              <>
                <button
                  onClick={() => gameState.claimReward()}
                  disabled={gameState.isClaimingReward}
                  className="w-full font-press-start py-4 px-8 bg-transparent border-2 border-banana text-banana hover:bg-banana hover:text-royal transition-colors pixel-button"
                >
                  {gameState.isClaimingReward ? 'CLAIMING...' : 'CLAIM MINING REWARDS'}
                </button>
                <button
                  onClick={() => gameState.upgradeFacility()}
                  disabled={gameState.isUpgradingFacility}
                  className="w-full font-press-start py-4 px-8 bg-transparent border-2 border-banana text-banana hover:bg-banana hover:text-royal transition-colors pixel-button"
                >
                  {gameState.isUpgradingFacility ? 'UPGRADING...' : 'UPGRADE FACILITY'}
                </button>
              </>
            )}
          </div>
        </div>
      </main>
    </div>
  );
} 